.light-pathway {
    border: 1px solid $bga-blue-border;
    background: $bga-white;
    display: block;

    &:focus,
    &:hover {
        color: $bga-black;
        background: $bga-blue-background-extralight;
    }
    &:active {
       border: 1px solid $bga-blue-border !important; 
    }

    .top-icon {
        background: $bga-blue-black-mid;
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100px;
        
        img {
            width: auto;
            max-width: auto;
        }
    }
    .component-content {
        border-left: 8px solid $bga-blue-border;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;
    }
    .top-wrapper {
        flex-basis: 100%;
    }
    .component-text {
        width: 100%;
        display: block;
    }
    .tile-title {
        font-size: $h4-font-size;
        line-height: $headings-line-height;
    }
    .arrow-icon {
        width: 32px;
        height: 32px;
        background: $bga-blue-background-light;
        border-radius: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        position:absolute;
        top: center;
        left: 0;

        img {
            width: 16px;
            height: auto;
        }
    }
    .icon-wrapper {
        display: block;
        position: relative;
        min-width: 32px;
        min-height: 32px;
    }
    &.external {
        .icon-wrapper {
            display: block;
            position: relative;
            border-radius: 50px;
            display: flex;
            align-items: center;
            min-width: 32px;
            min-height: 32px;

            
            .external-site {
                background: $bga-blue-background-extralight;
                min-height: 32px;
                border-radius: 50px;
                padding: 0 0 0 40px;
                width: 100%;
                display: flex;
                align-items: center;
                word-break: break-word;
            }
            p {
                padding-top: 4px;
                padding-bottom: 4px;
                font-size: .75rem;
                line-height: 18px;
                font-weight: 600;
                word-break: break-word;
            }
        }
    }
    
    &:hover {
        .arrow-icon {
            background: $bga-blue-border;
        }
        &.external {
            .icon-wrapper {
                .external-site {
                    background: $bga-white;
                }
            }
        }
    }
    &.yellow {
        .top-icon {
            background: $bga-yellow-highlight-shadow;
        }
    }
    &.orange {
        .top-icon {
            background: $bga-orange-highlight-shadow;
        }
    }
    &.green {
        .top-icon {
            background: $bga-green-highlight-shadow;
        }
    }
    &.purple {
        .top-icon {
            background: $bga-purple-highlight-shadow;
        }
    }
    &.teal {
        .top-icon {
            background: $bga-teal-highlight-shadow;
        }
    }
    &.fb-blue {
        .top-icon {
            background: #1877f2;
        }
    }
    &.twitter-blue {
        .top-icon {
            background: #1d9bf0;
        }
    }
    &.youtube-red {
        .top-icon {
            background: #ff0000;
        }
    }
    &.linkedin-blue {
        .top-icon {
            background: #2867b2;
        }
    }

}
