.sidebar {
    background: $bga-teal-highlight-light;
}

.sidebar-wrapper {
    height: 100%;
    width: 100%;
    justify-content: space-between;
    height: 100%;

    &.teal {
        background: $bga-teal-highlight-light;
    }

    &.yellow {
        background: $bga-yellow-highlight-light;
    }

    &.blue {
        background: $bga-blue-background-light;
    }

    .component-text {
        overflow: hidden;
        position: relative;

        &.fixed-height {

            &::after {
                content: "";
                width: 100%;
                height: 48px;
                background: linear-gradient(0deg, rgba(207, 235, 235, 1) 30%, rgba(207, 235, 235, 0) 100%);
                bottom: 0;
                position: absolute;
            }
        }
    }

    .more-info-toggle {
        background: none;
        border: none;
        color: $bga-blue-links;
        text-decoration: underline;
        font-weight: 600;

        &.hide {
            display: none;
        }

    }

    &.extended {
        .component-text {
            height: 100% !important;

            &.fixed-height {
                &::after {
                    display: none;
                }
            }
        }

        .more-info-toggle {
            svg {
                transform: rotate(-90deg);
            }
        }
    }

    &.colour-example.gradient {
        .teal {
            background: $bga-teal-highlight-light;
            .component-text::after {
                content: "";
                width: 100%;
                height: 24px;
                background: linear-gradient(0deg, rgba(207, 235, 235, 1) 30%, rgba(207, 235, 235, 0) 100%);
                bottom: 0;
                position: absolute;
            }
        }

        .yellow {
            background: $bga-yellow-highlight-light;
            .component-text::after {
                content: "";
                width: 100%;
                height: 24px;
                background: linear-gradient(0deg, rgba(255, 243, 205, 1) 0%, rgba(255, 243, 205, 0) 100%);
                bottom: 0;
                position: absolute;
            }
        }

        .blue {
            background: $bga-blue-background-light;
            .component-text::after {
                content: "";
                width: 100%;
                height: 24px;
                background: linear-gradient(0deg, rgba(200, 217, 238, 1) 0%, rgba(200, 217, 238, 0) 100%);
                bottom: 0;
                position: absolute;
            }
        }
    }

    &.save-progress {
        height: auto !important;
        position: relative;
        max-width: 420px;

        .mobile-chat {
            display: none;
        }        
    }
}

@mixin sidebar-below768 {
    .sidebar-wrapper {
        &.save-progress {
            width: 100%;
            max-width: none;
            box-shadow: 0px -3px 3px rgba(51, 51, 51, 0.3);
            
            .component-content {
                padding-top: 16px !important;
            }

            .mobile-chat {
                display: block;
            }

            .component-text {
                display: none;
            }

            &.in-page {
                position: fixed;
                bottom: 0;
                left: 0;
            }
        }
    }
}

// SMALLER SCREENS display. Using classes instead of media queries because both breakpoint toggles and small size screens need to be taken into account.
.component-example.bp-below768 {
    @include sidebar-below768();
}


// Media queries for HTML example pages.
@media (max-width: 768px) {
    // just for in-page more info examples
    .sidebar-wrapper {
        margin: 32px 0 0 0 !important;

        &.colour-example {
            margin: 0 !important;
            padding-bottom: 32px !important;
        }
    }
    // In-page save your progress
    @include sidebar-below768();
}



